'use strict';

chrome.runtime.onInstalled.addListener(function (details) {
    console.log('previousVersion', details.previousVersion);
});

function onExecuted(result) {
    console.log('We made it green');
}

function onError(error) {
    console.log('Error: ' + error);
}

var makeItGreen = 'alert("test")';

function handleClick() {
    // alert('clicked browser action');
    chrome.tabs.query({
        currentWindow: true,
        active: true
    }, function (foundTabs) {
        var currentTabId = foundTabs[0].id;
        chrome.tabs.get(currentTabId, function (currentTab) {
            // const toggledValue = !currentTab.pinned;
            // chrome.tabs.update(currentTabId, {pinned: toggledValue});
            console.log('clicked browser action in tab ' + currentTabId);
        });
        var executing = browser.tabs.executeScript(currentTabId, {
            file: 'inject_google_translate_content.js'
        });
        executing.then(onExecuted, onError);
    });
}
browser.browserAction.onClicked.addListener(handleClick);

console.log('\'Allo \'Allo! Event Page for Browser Action');